import { Link } from 'react-router-dom';
import React, { useState } from 'react';
import PrintablesCarousel from '../components/PrintablesCarousel';
import { calculatePrintingPrice } from '../utils/cost-calculator';

import Main from '../layouts/Main';
import EmailLink from '../components/Contact/EmailLink';

const Printables = () => {
  const [formData, setFormData] = useState({
    material: 'PLA',
    hours: '0',
    minutes: '30',
    grams: '50',
  });

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }));
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    // console.log(formData); // Do something with the form data
    // console.log(Math.round(calculatePrintingPrice(
    //   formData.material,
    //   formData.hours,
    //   formData.minutes,
    //   formData.grams
    // )));
  };

  const materialId = 'material';
  const printTimeHoursId = 'hours';
  const printTimeMinutesId = 'minutes';
  const materialWeightGramsId = 'grams';

  return (
    <Main title='3D Printables' description='Calculators for 3D Printable Pricing'>
      <article className='post' id='printables'>
        <header>
          <PrintablesCarousel />
        </header>
        <article>
          <p>
            Welcome to my 3D printing and designing page! With the rise of advanced technologies, 3D printing has become a reality and has revolutionized the manufacturing industry.<br />
            From medical and food industries to product design prototyping in universities and industries, 3D printing has found its way into many industries worldwide. At my business, I use cutting-edge 3D printing technology to provide a range of services that traditional manufacturing methods cannot support.<br />
            With faster iteration to the design, we can create complex designs quickly and cost-effectively, making it ideal for prototyping products.<br /><br />
            I offer a range of services, including decorative design printing, replacement parts design and print, prototyping products, and case design and print. With almost a decade of experience, I have the skills and expertise to bring your ideas to life with precision and efficiency.
          </p>
          <p>
            Feel free to contact with any question, or send a design to get a price quote.
          </p>
          <p>
            <EmailLink />
          </p>
        </article>
        <hr />
        <h2>3D Print Calculator</h2>
        <p>if you know the values - set them and we&apos;ll do the rest to estimate the cost.</p>
        <p>if you don&apos;t, <Link to='/contact'>reach me</Link> and I&apos;ll do my best to help</p>
        <form onSubmit={handleSubmit}> {/* Add onSubmit attribute to the form */}
          <div>
            <span>
              <label htmlFor={materialId}>Material
                <select 
                  id={materialId} 
                  name={materialId} 
                  value={formData.material} 
                  onChange={handleChange}
                >
                  <option>PLA</option>
                  <option>PETG</option>
                  <option>ABS</option>
                </select>
              </label>
            </span>
          </div>
          <div>
            <span>
              <label htmlFor={printTimeHoursId}>Printing Time (Hours)
                <input 
                  type='number' 
                  id={printTimeHoursId} 
                  name={printTimeHoursId} 
                  value={formData.hours} 
                  onChange={handleChange} 
                />
              </label>
            </span>
          </div>
          <div>
            <span>
              <label htmlFor={printTimeMinutesId}>Printing Time (Minutes)
                <input 
                  type='number' 
                  id={printTimeMinutesId} 
                  name={printTimeMinutesId} 
                  value={formData.minutes} 
                  onChange={handleChange} 
                />
              </label>
            </span>
          </div>
          <div>
            <span>
              <label htmlFor={materialWeightGramsId}>Material Weight (Grams)
                <input
                  type='number'
                  id={materialWeightGramsId} 
                  name={materialWeightGramsId} 
                  value={formData.grams} 
                  onChange={handleChange} 
                />
              </label>
            </span>
          </div>
          {/* <button type='submit'>Calculate</button> */}
          {formData.hours && formData.minutes && formData.grams && (
            <div className='result'>
              <p>Printing Price (NIS): {Math.round(calculatePrintingPrice(formData.material, formData.hours, formData.minutes, formData.grams))}</p>
            </div>
          )}
        </form>
      </article>
    </Main>);
};

export default Printables;
