/* eslint-disable max-len,operator-linebreak */

const materialsPricePerK = {
  ABS: 120,
  PETG: 120,
  PLA: 80,
  TPU: 150,
};

const elecCost = {
  kWh: 0.46,
  materials: {
    ABS: 0.12,
    PETG: 0.1,
    PLA: 0.08,
    TPU: 0.1,
  },
};

const generalAdditions = {
  adhessive: 1,
  advertising: 3,
  convenienceFee: 15,
  hourlyDesignCost: 250,
  markupFactor: 4,
  printerWearCost: 7,
};

export const getMaterials = () => Object.keys(materialsPricePerK);

export const calculatePrintingPrice = (material, printTimeHours, printTimeMinutes, materialWeightGrams) => {
  const totalPrintingTime = printTimeHours + printTimeMinutes / 60;
  const electCost = elecCost.kWh * elecCost.materials[material] * totalPrintingTime;
  const materialCost = materialWeightGrams * (materialsPricePerK[material] / 1000);

  return (
    electCost +
    materialCost +
    generalAdditions.adhessive + generalAdditions.advertising +
    generalAdditions.printerWearCost) * generalAdditions.markupFactor +
    generalAdditions.convenienceFee;
};

export const calculatePrintingCost = (material, printTimeHours, printTimeMinutes, materialWeightGrams) => {
  const totalPrintingTime = printTimeHours + printTimeMinutes / 60;
  const electCost = elecCost.kWh * elecCost.materials[material] * totalPrintingTime;
  const materialCost = materialWeightGrams * (materialsPricePerK[material] / 1000);

  return electCost +
        materialCost +
        generalAdditions.adhessive +
        generalAdditions.advertising +
        generalAdditions.printerWearCost;
};
